<template>
   <div>
      <basic-header :items="['店铺管理','分类管理']"></basic-header>
      <el-card>
         <el-tabs v-model="activeName">
            <el-tab-pane label="分类查看" name="list">
               <el-table :data="tableData">
                  <el-table-column label="id" prop="id"></el-table-column>
                  <el-table-column label="名称" prop="name"></el-table-column>
                  <el-table-column label="图片" prop="thumb">
                     <template #default="o">
                        <img :src="o.row.thumb" alt="" width="100" height="100">
                     </template>
                  </el-table-column>
                  <el-table-column label="排序" prop="sort"></el-table-column>
                  <el-table-column label="创建时间" prop="create_time"></el-table-column>
                  <el-table-column label="更新时间" prop="update_time"></el-table-column>
                  <el-table-column label="操作">
                     <template #default="o">
                        <el-button type="text" @click="edit(o.row)">编辑</el-button>
                        <el-button type="text" @click="del(o.row.id)">删除</el-button>
                     </template>
                  </el-table-column>
               </el-table>
               <el-pagination
                 background
                 style="margin-top:30px;"
                 layout="prev,pager,next"
                 :total="total"
                 :page-size.sync="pageSize"
                 :current-page.sync="page"
                 @current-change="change"
               ></el-pagination>
            </el-tab-pane>
            <el-tab-pane label="分类添加" name="add">
               <el-col :span="12">
                  <el-form label-width="80px" :model="form" :rules="rules" ref="submitForm">
                     <el-form-item label="分类名称" prop="name">
                        <el-input v-model="form.name"></el-input>
                     </el-form-item>
                     <el-form-item label="分类图片" prop="thumb">
                        <el-upload
                                class="avatar-uploader"
                                action="/api/upload/upload"
                                :show-file-list="false"
                                :on-success="handleAvatarSuccess"
                                :before-upload="beforeAvatarUpload">
                           <img v-if="form.thumb" :src="form.thumb" class="avatar">
                           <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                     </el-form-item>
                     <el-form-item label="排序" prop="sort">
                           <el-input type="number" v-model="form.sort"></el-input>
                     </el-form-item>
                     <el-form-item>
                          <el-button type="primary" @click="submit">提交</el-button>
                     </el-form-item>
                  </el-form>
               </el-col>
            </el-tab-pane>
         </el-tabs>
      </el-card>
      <el-dialog
              title="提示"
              :visible.sync="dialogVisible"
              width="50%"
              >
         <el-form label-width="80px" :model="updateForm" :rules="rules" ref="updateForm">
            <el-form-item label="分类名称" prop="name">
               <el-input v-model="updateForm.name"></el-input>
            </el-form-item>
            <el-form-item label="分类图片" prop="thumb">
               <el-upload
                       class="avatar-uploader"
                       action="/api/upload/upload"
                       :show-file-list="false"
                       :on-success="handleAvatarSuccess2"
                       :before-upload="beforeAvatarUpload">
                  <img v-if="updateForm.thumb" :src="updateForm.thumb" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
               </el-upload>
            </el-form-item>
            <el-form-item label="排序" prop="sort">
               <el-input type="number" v-model="updateForm.sort"></el-input>
            </el-form-item>
         </el-form>
         <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="update">确 定</el-button>
  </span>
      </el-dialog>
   </div>
</template>
<script>
    import Header from "@/components/Header.vue"
    export default {
        data(){
            return {
                activeName:"list",
                tableData:[],
                page:1,
                pageSize:5,
                total:0,
                form:{
                    name:"",
                    thumb:"",
                    sort:""
                },
                rules:{
                    name:{required:true,message:"请输入分类名称",trigger:"blur"},
                    thumb:{required:true,message:"请上传分类图片"},
                    sort:{required:true,message:"请输入分类排序",trigger:"blur"},

                },
                api:"/api/type/type",
                dialogVisible:false,
                updateForm:{
                    name:"",
                    thumb:"",
                    sort:""
                }
            }
        },
        methods: {
            fetchData(){
               this.axios.get(this.api,{
                   params:{
                       page:this.page,
                       pageSize:this.pageSize,
                   }
               }).then(res=>{
                   if(res.data.code===200){
                       this.tableData=res.data.data;
                       this.total=res.data.total;
                   }
               })
            },
            change(){
               this.fetchData();
            },
            handleAvatarSuccess(res, file) {
               this.form.thumb=res;
               this.$refs.submitForm.validateField("thumb");
            },
            handleAvatarSuccess2(res, file) {
               this.updateForm.thumb=res;
               this.$refs.updateForm.validateField("thumb");
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isJPG) {
                    this.$message.error('上传头像图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            },
            submit(){
                this.$refs.submitForm.validate(valid=>{
                    if(valid){
                        this.axios.post(this.api,this.form).then(res=>{
                            if(res.data.code===200){
                                this.$message.success("提交成功");
                                this.$refs.submitForm.resetFields();
                                this.activeName="list";
                                this.fetchData();
                            }
                        })
                    }else{
                        this.$message.error('验证失败');
                    }
                })
            },
            del(id){
                this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete(this.api,{
                        params:{
                           id
                        }
                    }).then(res=>{
                       if(res.data.code===200){
                           this.$message.success("删除成功");
                           this.fetchData();
                       }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            edit(obj){
                this.updateForm=obj;
                this.dialogVisible=true;
            },
            update(){
                delete this.updateForm.create_time;
                this.axios.put(this.api,this.updateForm).then(res=>{
                    if(res.data.code===200){
                        this.dialogVisible=false;
                        this.fetchData();
                        this.$message.success("修改成功");
                    }
                })
            }
        },
        components:{
          "basic-header":Header
        },
        created(){
           this.fetchData();
        },
        mounted(){

        }
    }
</script>
<style scoped>
   .avatar-uploader .el-upload {
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
   }
   .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
   }
   .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 178px;
      height: 178px;
      line-height: 178px;
      text-align: center;
      border: 1px dashed #d9d9d9;
   }
   .avatar {
      width: 178px;
      height: 178px;
      display: block;
   }
</style>  